.Cover {
  display: -ms-flexbox;
  display: flex;
  background: -webkit-radial-gradient(circle, white, #f0f0f0);
  background: -o-radial-gradient(circle, white, #f0f0f0);
  background: radial-gradient(circle, white, #f0f0f0);
  height: 100vh;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.Title {
  font-size: 3em;
  color: black;
  font-family: arial black;
  line-height: 1px;
}

.subTitle {
  margin-top: -10px;
  line-height: 2px;
  font-size: 1.1em;

}

.social {
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-flexbox;
  display: flex;
}

.logo {
  width: 55px;
  height: 55px;
}

.icon {
  margin-left: 5px;
  margin-right: 5px;
}

.profile {
  background-image: url("./assets/profile_image.jpg");
  background-size: contain;
  border: 5px solid black;
  height: 125px;
  width: 125px
}

.content {
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column
}



